	export default {
		data() {
			return {
                  goodsList:[]
			}
		},
		methods: {

		},
		mounted(){
			this.request({
				url:this.config.url+"order/service",
				method:"POST",
				data:{
					token:this.token,
					
				},
				success:res=>{
					console.log(res)
					if(res.data.code==1){
						this.goodsList=res.data.data.data;
					}
				}
			})
		}
	}
